<template>
	<div>
		<swiper :options="swiperOption" ref="mySwiper">
    <!-- slides -->
		    <swiper-slide><img :src="this.obj[0].img1" alt="tu"></swiper-slide>
		    <swiper-slide><img :src="this.obj[0].img2" alt="tu"></swiper-slide>
		    <swiper-slide><img :src="this.obj[0].img3" alt="tu"></swiper-slide>
		    <swiper-slide><img :src="this.obj[0].img4" alt="tu"></swiper-slide>
  
    <!-- Optional controls -->
	    <div class="swiper-pagination"  slot="pagination"></div>
	   
	  </swiper>
			
     
	</div>
</template>
<script>
export default{
	name: 'carrousel',
    data(){
      	return {
	      	obj:{},
	       	  swiperOption: {
	          loop:true, // true looploopslideslideSwiper 
				//loopfreefreeslide
	          notNextTick: true,
	          autoplay: 3000,  //
	          direction : 'horizontal', //Slides(horizontal)(vertical)
	          grabCursor : true,
	          setWrapperSize :true,
	          autoHeight: true,
	          pagination : '.swiper-pagination',
	          paginationClickable :true,
	        	
	          mousewheelControl : true,
	          observeParents:true,
	         
	          debugger: true,
	        
	          onTransitionStart(swiper){
	           
	          },
	        
	        }
    	}
    },
	
  	mounted(){
      this.$http.get("../static/Mall.json").then((response)=>{
        this.obj=JSON.parse(response.bodyText)
        },(response)=>{
      })
  	}

       

}

</script>
<style scoped>
	.store_content{width:15rem;height:auto}
    .swiper-container{width:100%;height:5.12rem}
    .swiper-container img{width: 100%;height: 100%;}
    .swiper-pagination-bullet{width:.4rem;height:.4rem}


</style>


